import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)


export default new Router({
	// base: process.env.BASE_URL,
	// mode: 'history',
	routes: [{ //首页的路由
			path: '/',
			name: 'home',
			component: Home,
		},
		{ //App制作的路由
			path: '/make/:orderid',
			name: 'make',
			component: () => import('./views/Make.vue')
		},
		{ //制作案例的路由-花店
			path: '/MakeCase',
			name: 'MakeCase',
			component: () => import('./views/Make_case.vue')
		},

		//待定
		// {//制作案例的路由-音乐
		//   path: '/make/:orderid',
		//   name: 'make',
		//   component: () => import('./views/Make-case01.vue')
		// },
		// {//制作案例的路由-租房
		//   path: '/make/:orderid',
		//   name: 'make',
		//   component: () => import('./views/Make-case01.vue')
		// },
		// {//制作案例的路由-企业官网
		//   path: '/make/:orderid',
		//   name: 'make',
		//   component: () => import('./views/Make-case01.vue')
		// },
		{ //新闻动态的路由
			path: '/news/:orderid',
			name: 'news',
			meta: {
				keepAlive: true
			},
			component: () => import('./views/News.vue'),
			redirect: '/news/5',
			children: [{
					path: '/',
					name: 'industryNew',
					meta: {
						keepAlive: true
					},
					component: resolve => require(['./components/industryNews.vue'], resolve),
				},
				{
					path: '/news/5/industryNews',
					name: 'industryNews',
					meta: {
						keepAlive: true
					},
					component: resolve => require(['./components/industryNews.vue'], resolve),
				},
				{
					path: '/news/5/enterpriseNews',
					name: 'enterpriseNews',
					meta: {
						keepAlive: true
					},
					component: resolve => require(['./components/enterpriseNews.vue'], resolve),
				},
				{
					path: '/news/5/developmentNews',
					name: 'developmentNews',
					meta: {
						keepAlive: true
					},
					component: resolve => require(['./components/developmentNews.vue'], resolve),
				}
			]
		},
		{ //网站开发的路由
			path: '/web/:orderid',
			name: 'web',
			component: () => import('./views/Web.vue')
		},
		{
			path: '/about',
			name: 'about',
			component: () => import('./views/About.vue')
		},
		{ //小程序的路由
			path: '/wxapp/:orderid',
			name: 'wxapp',
			component: () => import('./views/Wxapp.vue')
		},
		{ //小程序的二级详情页面
			path: '/wxstage',
			name: 'wxstage',
			component: () => import('./views/WxStage.vue'),
			meta: {
				keepAlive: true // 需要被缓存
			}
		},
	]
})
