<template>
	<div class="horselight">
		<el-carousel :interval="5000" arrow="always" v-if="typeNum == undefined">
			<el-carousel-item v-for="(item,i) in bannerImgs" :key="item.id" class="imgSize" v-if="i<3">
				<img :src=item.picUrl alt="">
			</el-carousel-item>
		</el-carousel>
		<img :src='imgUrl' v-else>
	</div>
</template>

<script>
	export default {
		//这是轮播图
		name: 'v-horselight',
		data() {
			return {
				bannerImgs: null,
				typeNum:undefined,
				imgUrl:null,
			}
		},
		methods: {
			postBanner() {
				var typeNum  = this.$route.params.orderid
				this.typeNum = typeNum
				
				// if(typeNum == undefined){
				// 	this.bannerImgs = this.$options.data().bannerImgs;
				// 	return;
				// }
				this.$http.post(process.env.VUE_APP_URL + '/picture/selectByType?type=1', {}, {
					emulateJSON: true
				}).then(function(result) {
					this.bannerImgs = result.body.data
					switch(Number(typeNum)){
						case 2:
						this.imgUrl = result.body.data[3].picUrl
						break;
						case 3:
						this.imgUrl = result.body.data[4].picUrl
						break;
						case 4:
						this.imgUrl = result.body.data[5].picUrl
						break;
						case 5:
						this.imgUrl = result.body.data[6].picUrl
						break;
					}
				})
			}
		},
		mounted: function() {
			this.postBanner()
		},
		props: {
			msg: String
		}
	}
</script>

