import Vue from 'vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import  router from './router.js'
import App from './App.vue'

import VueResource from 'vue-resource'
Vue.use(VueResource)

import jquery from 'jquery'
window.jquery = window.$ = jquery
//公共过滤器文件
import * as filters from './assets/js/filters'
Object.keys(filters).forEach(key => {
 Vue.filter(key, filters[key])
})

// 点击链接跳转回顶部
router.beforeEach((to, from, next) => {  
	if (!to.meta.keepAlive) {
		// chrome
		document.body.scrollTop = 0
		// firefox
		document.documentElement.scrollTop = 0
		// safari
		window.pageYOffset = 0
	}
    next()
})



Vue.config.productionTip = false
Vue.use(ElementUI);

const API="47.56.107.186:9090/demo";

Vue.prototype._API=API;

new Vue({
 router,
 watch:{
 	'$route.path':function(newVal,oldVal){
		if(newVal == "/Wxapp"){
			this.$router.push({name:"wxapp",params:{orderid:2}})
		}else if(newVal == "/Web"){
			this.$router.push({name:"web",params:{orderid:3}})
		}else if(newVal == "/Make"){
			this.$router.push({name:"make",params:{orderid:4}})
		}else if(newVal == "/News"){
			this.$router.push({name:"news",params:{orderid:5}})
		}
 	},
mounted:function(){
	console.log(this.$route.path)
}
 },
  render: h => h(App),
}).$mount('#app')